<template>
    <div class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50" @click.self="$emit('close')">
        <div class="bg-white p-4 rounded-lg shadow-lg">
            <h2 class="text-xl font-bold">Editar Respuesta</h2>
            <form @submit.prevent="updateReply(reply)">
                <span class="font-bold flex m-2 p-2">@{{ reply.autor }}</span>
                <textarea v-model="reply.mensaje" placeholder="Mensaje" class="w-full p-2 border rounded mt-2"></textarea>
                <div class="mt-4">
                    <button type="submit" class="bg-blue-500 text-white px-4 py-2 rounded">Guardar Cambios</button>
                    <button @click="close" class="bg-red-500 text-white px-4 py-2 rounded ml-2">Cancelar</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script setup lang="ts">
import { defineProps, defineEmits, ref } from "vue";
import { Reply } from "../interfaces/Reply";
import homeService from "../service/homeService";
import Swal from "sweetalert2";
const props = defineProps<{ reply: Reply }>();
const emit = defineEmits<{ (event: "close"): void; (event: "replyUpdated", reply: Reply): void }>();

const reply = ref({ ...props.reply });

async function updateReply(reply: Reply) {
    try {
        const response = await homeService.updateReply(reply.id, reply.mensaje);
        console.log("recibido: ", response.data);
        Swal.fire({
            title: "Edicion exitosa!",
            text: "Respuesta Editada",
            icon: "success",
            confirmButtonText: "Ok"
        });
    } catch (error) {
        console.error("error", error);
    }

    emit("replyUpdated", reply);
    emit("close");
}

function close() {
    emit("close");
}
</script>

<style scoped></style>
