<template>
    <form
        @submit.prevent="handleSubmit()"
        class="rounded-lg fixed left-[50%] top-[50%] z-50 grid w-full max-w-lg translate-x-[-50%] translate-y-[-50%] gap-4 border bg-background p-6 shadow-lg duration-200 data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[state=closed]:slide-out-to-left-1/2 data-[state=closed]:slide-out-to-top-[48%] data-[state=open]:slide-in-from-left-1/2 data-[state=open]:slide-in-from-top-[48%] sm:rounded-lg sm:max-w-[425px]"
        style="pointer-events: auto"
    >
        <div class="flex flex-col space-y-1.5 text-center sm:text-left">
            <h2 class="whitespace-nowrap text-lg font-semibold leading-none tracking-tight">Crear Nuevo Curso</h2>
        </div>
        <div class="grid gap-4 py-4">
            <div class="grid items-center grid-cols-4 gap-4">
                <label class="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 text-right"
                    >Nombre</label
                ><input
                    class="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 col-span-3"
                    v-model="nombre"
                    placeholder="Nombre del curso"
                    required
                />
            </div>
            <div class="grid items-center grid-cols-4 gap-4">
                <label class="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 text-right"
                    >Descripcin</label
                ><textarea
                    class="flex min-h-[80px] w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 col-span-3"
                    v-model="descripcion"
                    required
                    placeholder="Descripcin del curso"
                ></textarea>
            </div>
        </div>
        <div class="flex justify-end space-x-2 sm:flex-grow sm:justify-end sm:space-x-2">
            <button
                type="submit"
                class="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2"
            >
                Crear
            </button>
            <div class="grid">
                <button
                    @click.self="$emit('close')"
                    class="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-background hover:bg-accent hover:text-accent-foreground h-10 px-4 py-2"
                >
                    Cancelar
                </button>
            </div>
        </div>
    </form>
</template>

<script lang="ts" setup>
import authService from "@/components/auth/service/authService";
import { Curso } from "@/components/home/interfaces/Curso";
import homeService from "@/components/home/service/homeService";
import axios from "axios";
import Swal from "sweetalert2";
import { ref, defineEmits, reactive } from "vue";

const nombre = ref("");
const descripcion = ref("");

const emit = defineEmits(["courseCreated", "close"]);
async function handleSubmit() {
    console.log("Curso creado");
    try {
        const response = await homeService.createCourses(nombre.value, descripcion.value);
        const newCourse: Curso = response.data;
        Swal.fire({
            title: "Registro exitoso!",
            text: "Se ha creado nuevo Curso.",
            icon: "success",
            confirmButtonText: "Ok"
        });
        emit("courseCreated", newCourse); // Emitir evento con el nuevo curso
        emit("close"); // Cerrar el modal despus de crear el curso
    } catch (e) {
        //    console.error("Error creating curso:", error);

        if (axios.isAxiosError(e)) {
            if (e.response?.status == 409) {
                Swal.fire({
                    title: "Error!",
                    text: e.response.data,
                    icon: "error",
                    confirmButtonText: "Ok"
                });
            }
        }
    }
}
</script>

<style scoped></style>
