<template>
    <div class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50" @click.self="$emit('close')">
        <div class="bg-white p-4 rounded-lg shadow-lg">
            <h2 class="text-xl font-bold">Editar Topico</h2>
            <form @submit.prevent="updateTopic(topic)">
                <input v-model="topic.titulo" placeholder="Titulo" class="w-full p-2 border rounded mt-2 font-bold" />
                <textarea v-model="topic.mensaje" placeholder="Mensaje" class="w-full p-2 border rounded mt-2"></textarea>
                <div class="mt-4">
                    <button type="submit" class="bg-blue-500 text-white px-4 py-2 rounded">Guardar Cambios</button>
                    <button @click="close" class="bg-red-500 text-white px-4 py-2 rounded ml-2">Cancelar</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, defineProps, defineEmits } from "vue";
import { Topic } from "../interfaces/Topic";
import homeService from "../service/homeService";
import Swal from "sweetalert2";
const props = defineProps<{ topic: Topic }>();
const emit = defineEmits<{ (event: "close"): void; (event: "topicUpdated", topic: Topic): void }>();

const topic = ref({ ...props.topic });

async function updateTopic(topic: Topic) {
    try {
        const response = await homeService.updateTopic(topic.id, topic.titulo, topic.mensaje);
        console.log("recibido: ", response.data);
        Swal.fire({
            title: "Edicion exitosa!",
            text: "Topico Editado",
            icon: "success",
            confirmButtonText: "Ok"
        });
    } catch (error) {
        console.error("error", error);
    }

    emit("topicUpdated", topic);
    emit("close");
}

function close() {
    emit("close");
}
</script>

<style scoped></style>
