<template>
    <div class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
        <div class="bg-white p-4 rounded-lg shadow-lg">
            <h2 class="text-xl font-bold">Confirma Borrado</h2>
            <p>Estas Seguro que Quieres Borrar este Topico?</p>
            <div class="mt-4">
                <button @click="confirmDelete(props.topicId)" class="bg-red-500 text-white px-4 py-2 rounded">Borrar</button>
                <button @click="close" class="bg-gray-500 text-white px-4 py-2 rounded ml-2">Cancelar</button>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { defineProps, defineEmits, reactive } from "vue";
import { Topic } from "../interfaces/Topic";
import homeService from "../service/homeService";
import Swal from "sweetalert2";

const props = defineProps<{ topicId: number }>();

const emit = defineEmits<{ (event: "close"): void; (event: "topicDeleted", topicId: number): void }>();

async function confirmDelete(id: number) {
    console.log("topico a elimnar:", id);
    try {
        const response = await homeService.deleteTopic(id);
        console.log("respuesta: ", response.data);
        Swal.fire({
            title: "Eliminacion exitosa!",
            text: response.data,
            icon: "success",
            confirmButtonText: "Ok"
        });
        emit("topicDeleted", id);
        emit("close");
    } catch (error) {
        console.error("error", error);
    }
}

function close() {
    emit("close");
}
</script>

<style scoped></style>
