<template>
    <div class="flex h-screen w-full flex-col">
        <HeaderComponent></HeaderComponent>
        <main class="flex-1 bg-gray-100 p-6">
            <div class="container mx-auto replycss">
                <div class="mb-6 flex items-center justify-between">
                    <h1 class="text-2xl text-black font-bold">
                        <router-link :to="`/cursos/${idCurso}/topics`" class="flecha">
                            <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                            </svg>
                        </router-link>
                        Respuestas de {{ replyoSelecionado }}
                    </h1>
                </div>
                <div class="bg-white p-4 rounded shadow mb-4">
                    <h3 class="font-bold">@{{ byUsuario }}</h3>
                    <p class="mb-4">{{ mensaje }}</p>
                </div>
                <div v-for="reply in replies" :key="reply.id" class="grid gap-3">
                    <hr />
                    <div
                        class="flex flex-col sm:flex-row justify-between items-start sm:items-center bg-accent replycss group rounded-lg shadow-md p-4"
                    >
                        <div>
                            <span class="font-bold">@{{ reply.autor }}</span>
                            <p>{{ reply.mensaje }}</p>
                        </div>
                        <div v-if="reply.autor === currentUser" class="flex space-x-2 mt-2 sm:mt-0">
                            <button
                                @click.prevent="showEditReplyModal(reply)"
                                class="bg-white border border-black text-black hover:bg-black hover:text-white p-1 rounded"
                            >
                                Editar
                            </button>
                            <button
                                @click.prevent="showConfirmDeleteModal(reply.id)"
                                class="bg-white border border-red-700 text-red-700 hover:text-white hover:bg-red-700 p-1 rounded"
                            >
                                Borrar
                            </button>
                        </div>
                    </div>
                </div>
                <div class="mt-6">
                    <textarea
                        class="flex min-h-[80px] bg-background text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 w-full rounded-lg border border-gray-300 p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                        placeholder="Escribir su respuesta..."
                        v-model="respuesta"
                    ></textarea>

                    <div class="pt-3">
                        <button
                            @click.prevent="publicar()"
                            class="bg-black text-white font-medium font-serif hover:opacity-80 transition-opacity inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2"
                        >
                            Enviar Respuesta
                        </button>
                    </div>
                </div>
            </div>
        </main>
        <footer class="bg-gray-900 py-4 px-6 text-white">
            <div class="container mx-auto text-center"> 2023 Forum. All rights reserved.</div>
        </footer>
    </div>
    <EditReplyModal v-if="showEditReply && selectedReply" :reply="selectedReply" @close="hideEditReplyModal" @replyUpdated="onReplyUpdated" />
    <ConfirmDeleteReplyModal
        v-if="showConfirmDelete && replyIdToDelete"
        :replyId="replyIdToDelete"
        @close="hideConfirmDeleteModal"
        @replyDeleted="onReplyDeleted"
    />
</template>

<script lang="ts" setup>
import { onMounted, ref, defineProps } from "vue";
import HeaderComponent from "../components/home/page/HeaderComponent.vue";
import { Reply } from "@/components/home/interfaces/Reply";
import HomeService from "../components/home/service/homeService";
import homeService from "../components/home/service/homeService";
import Swal from "sweetalert2";
import { jwtDecode } from "jwt-decode";
import EditReplyModal from "../components/home/page/EditReplyModal.vue";
import ConfirmDeleteReplyModal from "../components/home/page/ConfirmDeleteReplyModal.vue";

const replies = ref<Reply[]>([]);
const replyoSelecionado = ref("");
const mensaje = ref("");
const respuesta = ref("");
const idCurso = ref("");
const byUsuario = ref("");
const currentUser = ref("");
const showEditReply = ref(false);
const showConfirmDelete = ref(false);
const replyIdToDelete = ref<number | null>(null);
const selectedReply = ref<Reply | null>(null);
const props = defineProps<{ id: number }>();
function showEditReplyModal(reply: Reply) {
    selectedReply.value = { ...reply };
    showEditReply.value = true;
}
function hideEditReplyModal() {
    showEditReply.value = false;
}
function showConfirmDeleteModal(replyId: number) {
    replyIdToDelete.value = replyId;
    showConfirmDelete.value = true;
}
function hideConfirmDeleteModal() {
    showConfirmDelete.value = false;
}
const fetchReplies = async () => {
    try {
        const response = await HomeService.getRepliesBoard(props.id);
        console.log("response:", response);
        if (response.data && response.data.respuestas) {
            replies.value = response.data.respuestas;
            replyoSelecionado.value = response.data.titulo;
            mensaje.value = response.data.mensaje;
            byUsuario.value = response.data.usuario;
            idCurso.value = response.data.idCurso;
        }
    } catch (error) {
        console.error("Error fetching replies:", error);
    }
};

async function publicar() {
    console.log("ID:", props.id);
    try {
        const response = await homeService.createReplies(props.id, respuesta.value);

        if (response.data) {
            const newRespuesta: Reply = response.data;
            replies.value.push(newRespuesta); // Add new reply to replies array
            respuesta.value = ""; // Clear the textarea

            Swal.fire({
                title: "Registro exitoso!",
                text: "Se ha creado nueva Respuesta.",
                icon: "success",
                confirmButtonText: "Ok"
            });
        }
    } catch (error) {
        Swal.fire({
            title: "Error!",
            text: "No se pudo agregar la respuesta",
            icon: "error",
            confirmButtonText: "Ok"
        });
    }
}
function onReplyUpdated(reply: Reply) {
    const index = replies.value.findIndex((t) => t.id === reply.id);
    if (index !== -1) {
        replies.value[index] = reply;
    }
}

function onReplyDeleted(replyId: number) {
    replies.value = replies.value.filter((reply) => reply.id !== replyId);
}

onMounted(() => {
    const token = localStorage.getItem("token");
    if (token) {
        const decodedToken: { sub: string } = jwtDecode(token);

        if (decodedToken.sub) {
            currentUser.value = decodedToken.sub;
        }
    }
});
onMounted(fetchReplies);
</script>

<style scoped>
.replycss {
    text-align: -webkit-left;
}
.fle {
    display: ruby-text;
}
.flecha {
    padding-right: 0.5rem;
    display: ruby;
}
</style>
